import React from 'react'
import Header from '../../components/Home/HeaderSection/Header'
import Card_Title from './Components/Card_Title'
import Banner_Benefits from './Components/Banner_Benefits'
import Benefits_of_ordereing from './Components/Benefits_of_ordereing'
import How_It_Works from './Components/How_It_Works'
import Org_Benefits from './Components/Org_Benefits'
import FooterComponent from '../../components/Common/Footer/footer';
import { DoctorConsult_Data } from './Data/DoctorConsultation_Data'

export default function DoctorConsult_Details() {
    return (
        <div>
              <Header />
            <Card_Title data={DoctorConsult_Data} />
            <Banner_Benefits data={DoctorConsult_Data}/>
            <Benefits_of_ordereing data={DoctorConsult_Data} />
            <How_It_Works data={DoctorConsult_Data} lists={true}/>

            <div align="center" style={{width: 'fit-content',margin: 'auto',marginTop: '20px'}}>
            <div className="disclain-txt frstline">*Disclaimer:  The scope of such discounts offered to the members is dynamic in nature, and bits-pilani and bits-pilani’s Partner Network reserve the right to change the discounts available from time to time, per bits-pilani sole discretion.  </div>
            </div>
            

            <Org_Benefits />
            <div className="footer-comp">
            <FooterComponent />
            </div>
        </div>
    )
}
