import Button from '../../../../Input-controls/Button/Button'
import React, { useState, useEffect } from 'react'
import Input from '../../../../Input-controls/Input/Input';
import './Gstin.scss';
import { notify } from '../../../../Input-controls/Toast/Toast';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';

function MpinModel(props) {

    

    const [state, setstate] = useState({
        name: ''
    })

    useEffect(() => {

        if(props.mid){
            setstate({name:props.mid})
        }
        
    }, [])

    const handleproceed=()=>{
        if(!state.name){
            notify.warning("Please enter the Mid")
        }else{
            props.proceedClicked(state.name)
        }
    }

    return (
        <>
			<div className="pp_prnt_1"/>
            <div className="pp_cls-invite">
                <div>
                    <Input 
                    type="text"
                    value={state.name}
                    onChange={(e) =>setstate({...state, name:e.target.value})}
                    className="mpin-input" 
                    placeholder="Enter your Magicpin Id"
                    label="Enter your Magicpin Id" />
                </div>
                <div style={{marginTop: '90px'}}>
                    <Button value="proceed" onClick={()=>handleproceed()} />
                </div>
            </div> 
        </>
    )
}



const mapStateToProps = (state) => {
    return {
      mid:state.userstate.mid,
  }}
  

  
  
  export default withRouter(connect(mapStateToProps, null)(MpinModel))